.siteNav {
  display: block;
  margin: 0;
  padding: 20px;
}

.navLink {
  margin: 0 10px;
  color: gray;
  text-decoration: none;
  transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.navLink:hover {
  color: salmon;
}

.activeNavLink {
  margin: 0 10px;
  text-decoration: none;
  color: salmon;
}
